@use './_reset';
@use './_mixin' as *;
@use './_common' as c;

.flex_wrap {
    display: block;

    @include pc {
        display: flex;
        justify-content: center;
        gap: calc(73.6 / c.$pcw) * 100%;
        padding-top: calc(244 / c.$pcw) * 100%;
    }
}

.main_1 {
    margin-top: calc(104 / c.$spw) * 100%;
    position: relative;

    &::after {
        content: "";
        position: absolute;
        width: calc(700 / c.$spw) * 100%;
        top: 0;
        left: 50%;
        transform: translateX(-50%);
        background: #fff;
        height: 100%;
        z-index: 1;
    }

    @include pc {
        margin-top: calc(170 / c.$pcw) * 100%;

        &::after {
            width: calc(2600 / c.$pcw) * 100%;
        }
    }
}

.style {
    position: relative;
    z-index: 100;
    line-height: 0;
}

.style_1 {
    width: calc(617.89 / c.$spw) * 100%;
    padding-top: calc(120.5 / c.$spw) * 100%;
    margin: 0 auto;

    @include pc {
        width: calc(845 / c.$pcw) * 100%;
        padding-top: 0;
        margin: 0;
    }
}

.flex-inner {
    @include pc {
        margin-top: calc(62 / c.$pcw) * 100%;
        width: calc(798.36/ c.$pcw) * 100%;
    }

}







.style_txt {
    position: relative;
    z-index: 100;
    font-size: calc(27 / c.$spw * 100vw);
    text-align: left;
    margin-left: calc(77.3 / c.$spw) * 100%;
    color: c.$txt;
    width: calc(638.01 / c.$spw) * 100%;
    margin-top: calc(243 / c.$spw) * 100%;
    line-height: calc(48 / 27);

    @include pc {
        font-size: min(calc(34 / c.$spw * 100vw), 17px);
        margin-left: 0;
        margin-top: 0;
        width: 100%;
        font-feature-settings: "palt";
    }

    .namiline-b {
        stroke-dashoffset: 716.2362060546875px;
        stroke-dasharray: 716.2362060546875px;
        -webkit-transition: stroke-dashoffset 1s cubic-bezier(0.47, 0, 0.745, 0.715) 0s;
        transition: stroke-dashoffset 1s cubic-bezier(0.47, 0, 0.745, 0.715) 0s;
    }

    .circle-b {
        stroke-dashoffset: 1231.0106201171875px;
        stroke-dasharray: 1231.0106201171875px;
        -webkit-transition: stroke-dashoffset 1s cubic-bezier(0.47, 0, 0.745, 0.715) 0s;
        transition: stroke-dashoffset 1s cubic-bezier(0.47, 0, 0.745, 0.715) 0s;
    }

    .doubleline-1 {
        stroke-dashoffset: 273.37554931640625px;
        stroke-dasharray: 273.37554931640625px;
        -webkit-transition: stroke-dashoffset 1s cubic-bezier(0.47, 0, 0.745, 0.715) 0s;
        transition: stroke-dashoffset 1s cubic-bezier(0.47, 0, 0.745, 0.715) 0s;
    }

    .doubleline-2 {
        stroke-dashoffset: 245.474609375px;
        stroke-dasharray: 245.474609375px;
        -webkit-transition: stroke-dashoffset 1s cubic-bezier(0.47, 0, 0.745, 0.715) 0.12s;
        transition: stroke-dashoffset 1s cubic-bezier(0.47, 0, 0.745, 0.715) 0.12s;
    }

    &.active {
        
        .circle-b {
            stroke-dashoffset: 0;
        }
        .namiline-b {
            stroke-dashoffset: 0;
        }

        .doubleline-1 {
            stroke-dashoffset: 0;
        }

        .doubleline-2 {
            stroke-dashoffset: 0;
        }
    }
}

