@use './_reset';
@use './_mixin' as *;


$spw : 750;
$pcw : 2732;

$main : rgba(0, 22, 137, 1);
$txt : rgba(0, 255, 0, 1);

body{
    background: $main;
}

.ja{
    font-family: zen-kaku-gothic-antique, sans-serif;
    font-weight: 400;
    font-style: normal;
}

.en_r{
    font-family: acumin-pro, sans-serif;
    font-weight: 400;
    font-style: normal;    
}
.en_m{
    font-family: acumin-pro, sans-serif;
    font-weight: 500;
    font-style: normal;    
}
.en_s{
    font-family: acumin-pro, sans-serif;
    font-weight: 600;
    font-style: normal;    
}
.en_b{
    font-family: acumin-pro, sans-serif;
    font-weight: 700;
    font-style: normal;    
}

@keyframes loop-text {
    0% {
        transform: translateX(0%);
    }
    
    100% {
        transform: translateX(-100%);
    }
}

@keyframes loop-text2 {
    0% {
        transform: translateX(-100%);
    }
    
    100% {
        transform: translateX(0%);
    }
}

.pc{
    display: none;
}
.sp{
    display: block;
}
@include pc{
    .pc{
        display: block;
    }
    .sp{
        display: none;
    }
}